import Vue from 'vue'
import router from '@/router'
import store from '@/store'

/**
 * uuid
 */
export function getUUID () {
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, c => {
    return (c === 'x' ? (Math.random() * 16 | 0) : ('r&0x3' | '0x8')).toString(16)
  })
}

/**
 * 
 * @param {*} key
 */
export function isAuth (key) {
  return JSON.parse(sessionStorage.getItem('permissions') || '[]').indexOf(key) !== -1 || false
}

/**
 * 
 * @param {*} data
 * mainKey 
 * @param {*} id
 * @param {*} parentId
 */
export function treeDataTranslate (data,mainKey,id,parentId,children) {
  let cloneData=JSON.parse(JSON.stringify(data))// 
  return cloneData.filter(father=>{// children
  	let branchArr=cloneData.filter(child => father[id] == child[parentId]); // 
  	if(branchArr.length>0){//children
  		father[children]=branchArr;
  	}
  	return father[parentId]==mainKey;//
  })
}

/**
 * 
 */
export function clearLoginInfo () {
  sessionStorage.removeItem('token')
  sessionStorage.removeItem('navSel')
  store.commit('resetStore')
  router.options.isAddDynamicMenuRoutes = false
}

/* 
	*arr
	*index1 
	*index2 
  *direction 
	*/
export function	swapItems(arr,index1,index2,direction) {
		if(direction=='top'){//
			arr.unshift(arr[index1]);
			arr.splice(index1+1,1);
			return arr;
		}else if(direction=='bottom'){//
			arr.push(arr[index1]);
			arr.splice(index1,1);
			return arr;
		}else{
			arr[index1]=arr.splice(index2,1,arr[index1])[0];
			return arr;
		}
	}

	export function arrYear(oldyear){
    var myDate = new Date();
    var thisYear = myDate.getFullYear();  // 
    var Section = thisYear - oldyear;  //   eg.2008
    var arrYear = []; //  
    for(var i = 0;i<=Section;i++){
      arrYear.push(thisYear--)
    } // 
    // arrYear2008
    return arrYear
  }



