	/**
	 * [{},{}]
	 * @param {Array} arr1 1
	 * @param {Array} arr2 2
	 * @param {String} primaryKey 
	 * @return {Array} 
	 */
	concatArray: function(arr1, arr2, primaryKey) {
		let arr3 = arr1.concat(arr2); //
		let newArr = []; //
		for(let item1 of arr3) { //json
			let flag = true; //true
			for(let item2 of newArr) { //
				if(item1[primaryKey] == item2[primaryKey]) { //jsonfalse
					flag = false;
				}
			}
			if(flag) { //
				newArr.push(item1); //  
			}
		}
		return newArr;
	},
	/**
	 * 
	 * @return {Array} 
	 */
	concatSimpleArray: function() {
		let newArr = Array.prototype.concat.apply([], arguments) //
		return Array.from(new Set(newArr))
	},
	/**
	 * 
	 * @param {Object} fileTypeId
	 * @return {Number} fileType   =1;  =2
	 */
	recognitionFileType: function(fileTypeId) {
		let fileType = 1; //
		if(fileTypeId == api.constObj.fileTypeList[0].id) {
			fileType = 2;
		}
		return fileType;
	},
	/**
	 * id(tree)
	 * @param {Array} arr 
	 * @param {String} nodeId id
	 * @param {Object} childToFatherObj 
	 * @param {Object} childToFatherObj.childKey 
	 * @param {Object} childToFatherObj.fatherKey 
	 * @return {Array} findNodeArray 
	 */
	findTreeNodeById: function(arr, nodeId, childToFatherObj) {
		var findNodeArray = [];
		var forFn = function(arr, pid) {
			for(var i = 0; i < arr.length; i++) {
				var item = arr[i];
				if(item[childToFatherObj.childKey] == pid) {
					findNodeArray.push(item);
					forFn(arr, item[childToFatherObj.fatherKey]);
				}
			}
		};
		forFn(arr, nodeId);
		return findNodeArray;
	},
	/**
	 * idid
	 * @param {Object} arr 
	 * @param {Object} idKey 
	 */
	createArrIdAndReverse: function(arr, idKey) {
		let returnArray = [];
		if(arr.length > 0) {
			arr.forEach(function(row, index) {
				returnArray.push(row[idKey]);
			})
			returnArray = returnArray.reverse();
		}
		return returnArray;
	},
	/**
	 * token
	 * @param {Boolean} isRedirect  
	 */
	againLogin(isRedirect) {
		let aDelStorage = [api.constObj.loginInfo,api.constObj.menuInfo,api.constObj.mainInfo,api.constObj.applyInfo,api.constObj.areaOrgInfo,api.constObj.printList,api.constObj.settingsList];
		window.vm.$store.commit("removeInfoByArray",aDelStorage);
		window.vm.$router.push({
			path: '/login'
		});
	},
	/**
	 * 
	 * @param {Array} totalList 
	 * @param {String} pageIndex 
	 * @param {String} pageSize 
	 */
	localPaging(totalList, pageIndex, pageSize) {
		let showList = totalList.slice(pageIndex * pageSize, (pageIndex + 1) * pageSize);
		return showList;
	},
	/**
	 * 
	 * @param {list} list 
	 * @param {ids} 
	 */
	joinTgetherId(list, ids, keyVal) {
		if(list == "") return ids = "";
		let arr = [];
		let keyStr = '';
		list.forEach(el => {
			for(let m in el) {
				if(m == keyVal) {
					keyStr += el[m] + ','
					arr.push(el[m])
				}
			}
		})
		return keyStr.substring(0, keyStr.length - 1);
	},
	/**
	 * vue
	 * @desc vuejson
	 * @param {Object||Array} vueData 
	 * @return {Object||Array} result 
	 */
	vueDataConverter(vueData) {
		var result = null;
		var type = Object.prototype.toString.call(vueData);

		switch(type) {
			case '[object Array]':
				result = toArray(vueData);
				break;

			case '[object Object]':
				result = toObject(vueData);
				break;

			default:
				result = vueData;
				break;
		}

		function toArray(vueArray) {
			var array = [];
			for(var index in vueArray) {
				var item = vueDataConverter(vueArray[index]);
				array.push(item);
			}
			return array;
		}

		function toObject(vueData) {
			var obj = new Object();
			for(var index in vueData) {
				var item = vueDataConverter(vueData[index]);
				obj[index] = item;
			}
			return obj;
		}
		return result;
	},
	/**
	 * 
	 * @param {String} val 
	 */
	checkIdCard(val){
		var p = /^[1-9]\d{5}(18|19|20)\d{2}((0[1-9])|(1[0-2]))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/;
		var factor = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2];
		var parity = [1, 0, 'X', 9, 8, 7, 6, 5, 4, 3, 2];
		var code = val.substring(17);
		if(p.test(val)) {
			var sum = 0;
			for(var i = 0; i < 17; i++) {
				sum += val[i] * factor[i];
			}
			if(parity[sum % 11] == code.toUpperCase()) {
				return true;
			}
		}
		return false;
	},
	/**
	 * 
	 * @param {Number} codeLength 
	 */
	computedChineseLen(codeLength){
		if(parseInt(codeLength) < api.constObj.chineseRatio){
			console.log(",!");
			return 0;
		}else{
			return parseInt(codeLength/api.constObj.chineseRatio);	
		}
		
	},
	/**
	 * 
	 * @param {Array} firstArray 
	 * @param {Array} secondArray 
	 * @return {Array} resultArray 
	 */
	arrayMergeDeDuplication(firstArray,secondArray){
		var aFirst = firstArray;
	    var aSecond = secondArray;
	    var resultArray = [];
	    var tmp = aFirst.concat(aSecond);
	    var o = {};
	    for (var i = 0; i < tmp.length; i ++) {
		   	if(tmp[i] in o){
		   		 o[tmp[i]] ++;
		   	}else{ 
		   		o[tmp[i]] = 1;
		   	}
	    }
	   for (let x in o){ 
	   		if (o[x] == 1){
	   			resultArray.push(x);
	   		}
	   }
	   return resultArray;
	},
	/**
	 * 
	 * @param {Array} columnsData 
	 */
	getExportParameters(columnsData){
		let params = {
			'titlels':"",
			"colsls":""
		};
		for(let i = 1; i < columnsData.length; i++) {
				if(columnsData[i]) {
					params.titlels += columnsData[i].label + ',';
					params.colsls += columnsData[i].property + ',';
				}
			}
			params.titlels = params.titlels.substring(0, params.titlels.lastIndexOf(','));
			params.colsls = params.colsls.substring(0, params.colsls.lastIndexOf(','));
		return params;
	},
